/* --------------------------------------------------------- */
/* ------------------------ GALLERY ------------------------ */
/* --------------------------------------------------------- */

.gallery {
    height: 100%;


    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    row-gap: 50px;
    column-gap: 60px;
    justify-items: center;

    padding: 56px 50px 0px 50px;

    border-radius: 25px;
    background: #F6F6F6;
}

@media only screen and (max-width: 600px) {
    .gallery {
        grid-template-columns: 1fr;
        row-gap: 20px;
        padding: 0;
        border-radius: 10px;
        background: none;
    }
}