.CarouselBtnContainer {
    position: absolute;
    height: 100%;
    display: flex;
    align-items: center
}

.LeftCarouselContainer {
    top: 0;
    left: 0;
    padding-left: 23.36px;
}

.RightCarouselContainer {
    top: 0;
    right: 0;
    padding-right: 23.36px;
}

.MediaContainer {
    height: 100%;
    border-radius: 25px;

}

.AppartmentImg {
    height: inherit;
    width: 100%;
    border-radius: 25px;
    object-fit: cover;
    background: lightgray 50% / cover no-repeat;
}

.CarrouselPositionIndicator {
    position: absolute;
    bottom: 24.92px;
    left: 50%;
    color: #FFF;
    text-align: center;
    font-family: Montserrat;
    font-size: 18px;
    font-style: normal;
    font-weight: 500;
    line-height: 142.6%;
}

@media only screen and (max-width: 600px) {
    .CarouselBtnContainer img {
        height: 24px;
        width: 24px;
    }

    .LeftCarouselContainer {
        padding-left: 0px;
    }

    .RightCarouselContainer {
        padding-right: 0px;
    }

    .MediaContainer {
        border-radius: 10px;
    }

    .AppartmentImg {

        border-radius: 10px;
    }

    .CarrouselPositionIndicator {
        display: none;
    }

}